<template>
  <div id='container' class='h-screen'>
    <div id='nav-bar-top' class='flex flex-row justify-between items-center h-8'>
      <div>Headline Wrapper (only for debugging purpose)</div>
      <button class='bg-blue-500 hover:bg-lime-600 px-6 h-full' @click='save'>Save</button>
    </div>
    <div id='cy'>

    </div>
    <PopupWindow
      v-if='windowEnabled'
      :action='action'
      :element-name='currentElement'
      :x='positionX'
      :y='positionY'
      @cancel='closeWindow'
      @action='actionWindow'></PopupWindow>
  </div>
</template>

<script>
import PopupWindow from '@/components/PopupWindow'
import cytoscape from 'cytoscape'
import EdgeHandleActions from '@/scripts/edgehandle-actions'
import { edgeHandleConf } from '@/scripts/edgehandle-conf'
import defaultStyle from '@/styles/default.json'

export default {
  name: 'GraphContent',


  inject: ['taxonomyDataMessage'],

  components: {
    PopupWindow,
  },

  data() {
    return {
      cyNodes: [],
      cyEdges: [],
      taxonomyString: this.taxonomyDataMessage,
      taxonomyData: undefined,
      taxonomyID: undefined,
      // edge handle add in
      eh: undefined,
      // cxtmenu addin
      nodeMenu: undefined,
      edgeMenu: undefined,
      coreMenu: undefined,
      renameMenu: undefined,
      // changing element
      windowEnabled: false,
      action: undefined,
      currentElement: undefined,
      positionX: undefined,
      positionY: undefined,

      // rename existing element
      oldNode: undefined,
    }
  },

  mounted() {
    this.setTaxonomyData()
  },

  methods: {
    setTaxonomyData: function() {
      this.taxonomyData = JSON.parse(this.taxonomyString)
      this.taxonomyID = this.taxonomyData.ID

      for (let [key, value] of Object.entries(this.taxonomyData)) {
        this.cyNodes.push({ data: { id: key, name: key } })
        value.forEach((child) => {
          this.cyEdges.push({
            data: { source: key, target: child, name: key + '->' + child },
          })
        })
      }
      this.cy = cytoscape({
        container: document.getElementById('cy'),

        //boxSelectionEnabled: true,
        //autounselectify: false,

        layout: {
          name: 'dagre',
          fit: true,
          spacingFactor: 1,
        },

        style: defaultStyle,

        elements: {
          nodes: this.cyNodes,
          edges: this.cyEdges,
        },
      })
      this.cy.nodes().addClass('node')

      this.initNodeMenu()
      this.initEdgeMenu()
      this.initCoreMenu()
      this.eh = this.cy.edgehandles(edgeHandleConf)
    },

    initNodeMenu: function() {
      // eslint-disable-next-line no-unused-vars
      this.nodeMenu = this.cy.cxtmenu({
        menuRadius: 75,
        selector: '.node',
        commands: [
          {
            fillColor: 'rgba(200, 200, 200, 0.75)',
            content: 'draw edge',
            contentStyle: {
              color: 'black',
            },
            select: (node) => {
              this.drawEdge(node.data().name, false)
            },
          },
          {
            fillColor: 'rgba(200, 200, 200, 0.75)',
            content: 'rename',
            contentStyle: {
              color: 'black',
            },
            select: (node) => {
              this.renameNode(node.data().name, node.position(), 'Rename')
            },
          },
          {
            fillColor: 'rgb(191,38,38)',
            content: 'delete',
            contentStyle: {
              color: 'black',
            },
            select: (ele) => {
              this.removeNode(ele.data().name)
            },
          },
        ],
        openMenuEvents: 'cxttapstart taphold',
      })
    },

    initEdgeMenu: function() {
      this.edgeMenu = this.cy.cxtmenu({
        menuRadius: 75,
        selector: 'edge',
        commands: [
          {
            fillColor: 'rgb(191,38,38)',
            content: 'delete',
            contentStyle: {
              color: 'black',
            },
            select: (edge) => {
              console.log('deleteEdge')
              EdgeHandleActions.removeEdge(this.cy, edge, this.taxonomyData)
            },
          },
          {
            fillColor: 'rgb(92,125,204)',
            content: 'rearrange',
            contentStyle: {
              color: 'black',
            },
            select: (edge) => {
              EdgeHandleActions.initTmpEdge(edge)
              EdgeHandleActions.removeEdge(this.cy, edge, this.taxonomyData)
              let source = edge.source().data().name
              this.drawEdge(source, true)
            },
          },
        ],
        openMenuEvents: 'cxttapstart taphold',
      })
    },

    initCoreMenu: function() {
      this.coreMenu = this.cy.cxtmenu({
        menuRadius: 75,
        selector: 'core',
        commands: [
          {
            fillColor: 'rgb(82,190,39)',
            content: 'add node',
            contentStyle: {
              color: 'black',
            },
            select: (ele, event) => {
              this.createNode(event)
            },
          },
        ],
        openMenuEvents: 'cxttapstart taphold',
      })
    },

    initRenameMenu: function() {
      this.renameMenu = this.cy.cxtmenu({
        selector: '.highlightNode',
        menuRadius: 75,
        commands: [
          {
            fillColor: 'rgb(191,38,38)',
            content: 'rename',
            contentStyle: {
              color: 'black',
            },
            select: (ele) => {
              console.log('rename', ele)
              this.renameNode(ele.data().name, ele.position(), 'Rename+')
            },
          },
          {
            fillColor: 'rgb(92,125,204)',
            content: 'merge',
            contentStyle: {
              color: 'black',
            },
            select: (ele) => {
              console.log('merge', ele)
              this.mergeNodes(ele.data().name)
            },
          },
        ],
        openMenuEvents: 'cxttapstart taphold',
      })
    },

    drawEdge: function(nodeName, rearrangeEdge) {
      EdgeHandleActions.drawEdge(
        nodeName,
        rearrangeEdge,
        this.cy,
        this.eh,
        this.taxonomyData,
      )
    },

    renameNode: function(element, position, mode) {
      console.log('starting rename procedure at ', position)
      console.log('x = ', position.x)
      this.cy.panningEnabled(false)
      this.action = mode
      this.currentElement = element
      this.positionX = position.x
      this.positionY = position.y
      this.windowEnabled = true
    },

    createNode: function(event) {
      this.cy.panningEnabled(false)
      this.action = 'Create Node'
      this.currentElement = ''
      this.positionX = event.position.x
      this.positionY = event.position.y
      this.windowEnabled = true
    },

    removeNode: function(element) {
      // get all edges from AND to selected node
      const edges = EdgeHandleActions.getAllEdges(this.cy, element)

      // remove edges
      if (edges.length > 0) {
        edges.forEach((element) => {
          let source = element.source().data().name
          let target = element.target().data().name
          this.cy.remove(
            'edge[source=\'' + source + '\'][target=\'' + target + '\']',
          )
          this.taxonomyData[source].splice(
            this.taxonomyData[source].indexOf(target),
            1,
          )
        })
      }
      // remove node
      this.cy.remove('node[name=\'' + element + '\']')
      delete this.taxonomyData[element]
    },
    // BUG: Rename Propeller to Controller, then Propeller -> Merge
    // unexpected behaviour
    mergeNodes: function(node, option) {
      if (this.taxonomyData[node] !== undefined) {
        // NODE selected -> (NODE) will be merged
        const oldNode = '(' + node + ')'
        const cyOldNode = this.cy.$('node[name=\'' + oldNode + '\']')
        // copy children from this.oldNode to NODE
        this.taxonomyData[this.oldNode].forEach(child => {
          this.taxonomyData[node].push(child)
        })

        // delete entry with child
        delete this.taxonomyData[this.oldNode]
        // delete parent entries with this.oldNode
        const targetEdges = EdgeHandleActions.getTargetEdges(this.cy, cyOldNode.id())
        console.log('name: ', cyOldNode.id())
        if (targetEdges.length > 0) {
          targetEdges.forEach(edge => {
            let source = edge.source().data().name
            this.taxonomyData[source].splice(
              this.taxonomyData[source].indexOf(this.oldNode),
              1,
            )
          })
        }
        // option occurs, when (NODE) is selected
        if (option !== undefined) node = option
        // copy children from cy.$oldNode to NODE
        const sourceEdges = EdgeHandleActions.getSourceEdges(this.cy, cyOldNode.id())
        if (sourceEdges.length > 0) {
          sourceEdges.forEach(edge => {
            console.log(node, '->', edge.target().data().name)
            this.cy.add({
              group: 'edges',
              data: {
                source: node,
                target: edge.target().data().name,
                name: node + '->' + edge.target().data().name,
              },
            })
          })
        }
        // remove oldNode from cy-instance
        this.removeNode(oldNode)
        const newNode = oldNode.slice(1, oldNode.length - 1)
        this.cy.$('node[name=\'' + newNode + '\']').classes('node')
        this.renameMenu.destroy()
        this.nodeMenu.destroy()
        this.initNodeMenu()
        console.table(this.taxonomyData)

      } else {
        // (NODE) selected -- NODE will be merged
        console.log('CASE 2: swap nodes')
        const sameNode = node.slice(1, node.length - 1)
        // just swap names
        const cX = this.cy.$('node[name=\'' + node + '\']')
        const cY = this.cy.$('node[name=\'' + sameNode + '\']')
        cX.json({ data: { name: sameNode } })
        cY.json({ data: { name: node } })
        this.mergeNodes(sameNode, this.oldNode)
      }
      this.oldNode = undefined
    },

    closeWindow: function() {
      console.log('cancel')
      this.windowEnabled = false
      this.cy.panningEnabled(true)
    },

    actionWindow: function(event) {
      console.log(event)
      switch (event.action) {
        case 'Rename':
          if (this.taxonomyData[event.newName] !== undefined) {
            // remove normal menu
            this.nodeMenu.destroy()
            this.cy
              .$('node[name=\'' + event.elementName + '\']')
              .classes('highlightNode')
            this.cy
              .$('node[name=\'' + event.newName + '\']')
              .classes('highlightNode')
            this.initNodeMenu()
            this.initRenameMenu()
            this.oldNode = event.elementName
            this.cy
              .$('[name=\'' + event.elementName + '\']')
              .json({ data: { name: '(' + event.newName + ')' } })
          } else {
            console.log('renaming ', event.elementName, ' to ', event.newName)
            this.taxonomyData[event.newName] = []
            this.addEdges(event.elementName, event.newName, this.taxonomyData)
            delete this.taxonomyData[event.elementName]
            this.cy
              .$('[name=\'' + event.elementName + '\']')
              .json({ data: { name: event.newName } })
            console.table(this.taxonomyData)
          }
          break
        case 'Rename+':
          console.log('entered!')
          // case 1: Rename Node with name switched to existing one
          if (event.elementName.startsWith('(')) {
            console.log('renaming ', this.oldNode, ' to ', event.newName)
            let oldName = event.elementName.slice(
              1,
              event.elementName.length - 1,
            )
            if (this.taxonomyData[event.newName] !== undefined)
              alert(
                event.newName +
                ' is already in scope(it may be a ghost copy of cytoscape)',
              )
            else {
              this.taxonomyData[event.newName] = []
              this.addEdges(
                event.elementName,
                event.newName,
                this.taxonomyData,
              )
              delete this.taxonomyData[this.oldNode]
              this.cy
                .$('[name=\'' + event.elementName + '\']')
                .json({ data: { name: event.newName } })
              this.oldNode = undefined
              this.renameMenu.destroy()
              this.nodeMenu.destroy()
              this.cy.$('node[name=\'' + oldName + '\']').classes('node')
              this.cy.$('node[name=\'' + event.newName + '\']').classes('node')
              this.initNodeMenu()
              // console.table(this.taxonomyData)
            }
          } else {
            // Case 2: Rename Node which was not touched initially
            console.log('old node:', this.oldNode)
            console.log('renaming ', event.elementName, ' to ', event.newName)
            // Error handling double names
            if (this.taxonomyData[event.newName] !== undefined)
              alert(
                event.newName +
                ' is already in scope(it may be a ghost copy of cytoscape)',
              )
            else {
              this.oldNode = undefined
              // Original Node name change
              this.actionWindow({
                action: 'Rename',
                elementName: event.elementName,
                newName: event.newName,
              })
              // (NODE) is logged in, add it like in Case 1
              this.actionWindow({
                action: 'Rename+',
                elementName: '(' + event.elementName + ')',
                newName: event.elementName,
              })
            }
          }
          break
        case 'Create Node':
          this.cy.add({ data: { id: event.newName, name: event.newName } })
          this.cy.$('node[name=\'' + event.newName + '\']').addClass('node')
          this.taxonomyData[event.newName] = []
          break
      }

      this.windowEnabled = false
      this.cy.panningEnabled(true)
    },

    addEdges: function(oldNode, newNode, taxonomyData) {
      const allEdges = this.cy
        .$('node[name=\'' + oldNode + '\']')
        .connectedEdges()
      allEdges.forEach((element) => {
        let edgeSource = element.source().data().name
        let edgeTarget = element.target().data().name
        if (edgeTarget === oldNode) {
          console.log('parent found (', edgeSource, ')')
          // in Rename+, the oldNode is already overwritten in a previous step
          if (this.oldNode !== undefined) {
            let indexParent = taxonomyData[edgeSource].indexOf(this.oldNode)
            taxonomyData[edgeSource][indexParent] = newNode
          } else {
            let indexParent = taxonomyData[edgeSource].indexOf(oldNode)
            taxonomyData[edgeSource][indexParent] = newNode
          }
        } else {
          console.log('child found (', edgeTarget, ')')
          taxonomyData[newNode].push(edgeTarget)
        }
      })
    },

    save: function() {
      adsk
        .fusionSendData('taxonomyDataMessage', JSON.stringify(this.taxonomyData))
        .then((result) => console.log(result))
    },
  },
}
</script>

<style scoped>

#cy {
  width: 100%;
  height: calc(100vh - 2rem);
  left: 0;
  top: 0;
  z-index: 100;
}
</style>
