import { createApp } from 'vue'
import App from './App.vue'
import cytoscape from 'cytoscape'
import dagre from 'cytoscape-dagre'
import edgehandles from 'cytoscape-edgehandles'
import cxtmenu from 'cytoscape-cxtmenu'
import './tailwind.css'

const app = createApp(App)
window.fusionJavaScriptHandler = {
  handle: function (action, data) {
    try {
      // console.log('action: ', action, '\ndata: ', data)
      if (action === 'updateMessage') {
        console.log('updated')
        // updateMessage(data);
      } else if (action === 'taxonomyDataMessage') {
        console.log('TaxonomyData arrived.')
        if (app.config.globalProperties.cy === undefined) {
          app.config.globalProperties.cy = cytoscape
          cytoscape.use(dagre)
          cytoscape.use(edgehandles)
          cytoscape.use(cxtmenu)
        }

        if (Object.keys(app._context.provides).length === 0) {
          app.provide('taxonomyDataMessage', data)
        }
        if (app._instance === null) {
          app.mount('#app')
        }
      } else if (action === 'taxonomyIDMessage') {
        console.log('taxonomyIDMessage arrived.')
        document.title = data
      } else if (action === 'debugger') {
        console.log('testdebugger')
        // eslint-disable-next-line no-debugger
        debugger
      } else {
        console.log('testelse')
        return `Unexpected command type: ${action}`
      }
    } catch (e) {
      console.log(e)
      console.log(`Exception caught with command: ${action}, data: ${data}`)
    }
    return 'OK'
  },
}
