import EdgehandlesColoring from '@/scripts/edgehandle-coloring'
import { checkForCircle } from '@/scripts/taxonomydata-constraints'
let validEdge
let tmpEdge

class EdgeHandleActions {
  static drawEdge(nodeName, rearrangeEdge, cy, eh, taxonomyData) {
    console.log(eh)
    const node = cy.$("node[name='" + nodeName + "']")
    eh.enableDrawMode()

    eh.start(node)

    cy.on('cxttap', () => {
      if (eh.drawMode) {
        eh.disableDrawMode()
        eh.stop()
      }
    })

    cy.on('ehhoverover', (event, sourceNode, targetNode) => {
      let x = checkForCircle(
        sourceNode.data().name,
        targetNode.data().name,
        taxonomyData
      )
      if (x) {
        console.log('circle found')
        EdgehandlesColoring.color(cy, 'red')
        validEdge = false
      } else {
        console.log('green arrow')
        EdgehandlesColoring.color(cy, 'green')
        validEdge = true
      }
    })

    cy.on('ehhoverout', () => {
      EdgehandlesColoring.color(cy, 'purple')
    })

    cy.on('ehcomplete', (event, sourceNode, targetNode, addedEdge) => {
      console.log('stopped edgecreation')
      console.log(
        'event:',
        event,
        '\nsource: ',
        sourceNode.data().name,
        '\ntarget: ',
        targetNode.data().name,
        '\nadded: ',
        addedEdge.source().data().name,
        '->',
        addedEdge.target().data().name
      )
      if (!validEdge) {
        console.log('remove invalid edge')
        cy.remove(
          "edge[source='" +
            sourceNode.data().name +
            "'][target='" +
            targetNode.data().name +
            "']"
        )
        if (rearrangeEdge) {
          this.recoverEdge(cy, taxonomyData)
        }
      } else {
        taxonomyData[sourceNode.id()].push(targetNode.id())
        cy.$(
          "edge[source='" +
            sourceNode.data().name +
            "'][target='" +
            targetNode.data().name +
            "']"
        ).data(
          'name',
          addedEdge.source().data().name + '->' + addedEdge.target().data().name
        )
      }
      console.table(taxonomyData)
    })

    cy.on('ehcancel', () => {
      console.log('cancelled')
      if (rearrangeEdge) {
        this.recoverEdge(cy, taxonomyData)
      }
    })

    cy.on('ehstop', () => {
      console.log('stopped.')
      rearrangeEdge = false
      eh.disableDrawMode()
      eh.stop()
      cy.off('ehstart')
      cy.off('ehcomplete')
      cy.off('ehhoverover')
      cy.off('ehhoverout')
      cy.off('ehcancel')
      cy.off('ehstop')
      cy.off('cxttap')
    })
  }

  static initTmpEdge(edge) {
    tmpEdge = edge
  }

  static getAllEdges(cy, node) {
    const edges = cy.edges("[source='" + node + "']")
    cy.edges("[target='" + node + "']").forEach((ele) => {
      edges.push(ele)
    })
    return edges
  }

  static getSourceEdges(cy, node) {
    const edges = cy.edges("[source='" + node + "']")
    return edges
  }
  static getTargetEdges(cy, node) {
    const edges = cy.edges("[target='" + node + "']")
    return edges
  }

  static removeEdge(cy, edge, taxonomyData) {
    let source = edge.source().data().name
    let target = edge.target().data().name
    taxonomyData[source].splice(taxonomyData[source].indexOf(target), 1)
    cy.remove("edge[name='" + edge.data().name + "']")
  }

  static recoverEdge(cy, taxonomyData) {
    cy.add({
      group: 'edges',
      data: {
        source: tmpEdge.source().data().name,
        target: tmpEdge.target().data().name,
        name:
          tmpEdge.source().data().name + '->' + tmpEdge.target().data().name,
      },
    })
    taxonomyData[tmpEdge.source().data().name].push(
      tmpEdge.target().data().name
    )
  }
}

export default EdgeHandleActions
