<template>
  <div class="w-100 bg-amber-400 p-1" :style="cssWindow">
    <div>{{ action }} {{ elementName }}</div>
    <input
      class="w-100"
      ref="focusMe"
      v-model="newName"
      @keydown.enter="doAction()" />
    <div class="flex flex-row place-content-between bg-gray-400">
      <button class="ml-2" @click="closeWindow">Cancel</button>
      <button class="mr-2" @click="doAction()">{{ action }}</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'PopupWindow',

    props: {
      action: undefined,
      elementName: undefined,
      x: Number,
      y: Number,
    },
    data() {
      return {
        newName: '',
      }
    },
    mounted() {
      this.$refs.focusMe.focus()
    },
    computed: {
      cssWindow() {
        return {
          position: 'absolute',
          left: this.x + -50 + 'px',
          top: this.y + 150 + 'px',
          'z-index': '1000',
        }
      },
    },
    methods: {
      closeWindow: function () {
        this.$emit('cancel')
      },
      // new Node, rename Node
      doAction: function () {
        if (this.newName !== '') {
          this.$emit('action', {
            action: this.action,
            elementName: this.elementName,
            newName: this.newName,
          })
        }
      },
    },
  }
</script>

<style></style>
