class EdgehandlesColoring {
  color(cy, color) {
    cy.style().selector('.eh-preview, .eh-ghost-edge').style({
      'line-color': color,
    })
    cy.style().selector('.eh-source').style({
      'border-color': color,
    })
    cy.style().selector('.eh-target').style({
      'background-color': color,
      'border-color': 'gray',
    })
    cy.style().selector('.eh-ghost-edge.eh-preview-active').style({
      opacity: 0,
    })
  }
}

export default new EdgehandlesColoring()
